.rank_image {
  width: 80%;
}
.rank_item_container {
  padding: 11px 11px 0 11px;
}
.rank_item {
  background: #16235b;
  display: flex;
  padding: 8px;
  border-radius: 5px;
}
.rank_image_container {
  width: 30%;
}
.rank_info_container {
  width: 70%;
  display: flex;
  flex-direction: column;
}
.rank_list_toptitle {
  font-size: 20px;
  font-weight: bold;
  color: #f7f1db;
  margin-bottom: 8px;
}
.rank_list_item_info {
  color: #f7f1db;
  margin-top: 4px;
  display: flex;
}
.rank_list_item_name {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: #a29e92;
}
.rank_list_item_song {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 50%;
  margin-right: 16px;
}
.rank_list_item_index {
  margin-right: 8px;
}
.rank_listen_count {
  display: flex;
  align-items: center;
  position: absolute;
  margin: -20px 0 0 5px;
  color: #fff;
  font-size: 12px;
}
.rank_listen_count img {
  width: 16px;
  margin-right: 4px;
}
